body, h1, button, p {
  margin: 0;
  padding: 0;
  border: none; }

body {
  background: #543e5c;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: #343C3E; }

input, textarea {
  border: none;
  outline: none;
  font: inherit;
  resize: none; }

button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  outline: none;
  background: transparent;
  color: inherit;
  font: inherit;
  cursor: pointer;
  line-height: normal; }
