form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0; }
  form label {
    font-size: 14px;
    font-weight: bold;
    padding: 3px 0px; }
  form input {
    padding: 5px;
    border-radius: 5px; }
  form .form-buttons {
    margin-top: 5px; }

.input-wrapper {
  display: flex;
  flex-direction: column; }

.form-input-fields {
  padding: 10px 0px; }

.error-message {
  color: red;
  font-size: 14px; }
